* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  text-decoration: none;
  list-style-type: none;
}
::-webkit-scrollbar {
  width: 0;
}
a,
svg {
  cursor: url(./Media/mouse-f2.png), auto;
}

html {
  cursor: url(./Media/mouse-f1.png), auto;
  overflow-x: hidden;
}
button {
  cursor: url(./Media/mouse-f2.png), auto;
}
#avatar {
  cursor: url(./Media/mouse-f2.png), auto;
  background: url(./Media/Spaceship.png);
  background-size: cover;
}
body {
  min-height: 100vh;
  min-width: 100%;
  transition: all 0.3s ease-in-out;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  background: #123;
}
#root {
  width: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  height: auto;
}
section {
  border-right: 1px dashed lightskyblue;
}
@keyframes move {
  0% {
    top: 0px;
  }
  25% {
    top: 10px;
  }
  50% {
    top: 0px;
  }
  75% {
    top: 10px;
  }
  100% {
    top: 0px;
  }
}
.Container {
  width: 550px;
  height: 425px;
  border: 3px solid lightskyblue;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.4s ease-in-out;
  border-radius: 8px;
  box-shadow: 15px 15px 0px 0px rgba(0, 0, 0, 0.1);
}
.Box-1,
.Box-2 {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.text:focus,
.text:hover {
  letter-spacing: 11px;
  transform: translateY(-5px);
}
.HeaderIcon {
  color: lightseagreen;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto;
  font-size: 30px;
  transition: 0.3s ease-in-out;
}
.HeaderIcon > svg:hover {
  color: cyan;
}
.text {
  background: linear-gradient(
    to right,
    cyan,
    lightseagreen,
    rgb(64, 124, 204),
    rgb(32, 212, 212)
  );
  background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: 0.4s ease;
  font-weight: 750;
  font-style: oblique;
  letter-spacing: 11px;
  font-size: 2.2em;
  cursor: url(./Media/mouse-f2.png), auto;
}
.progress-container {
  width: 100%;
  height: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: start;
}
.container-name {
  width: 90%;
  height: auto;
  font-style: oblique;
  font-weight: 550;
  letter-spacing: 2px;
  position: relative;
  height: 20%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
}
.progress {
  width: 90%;
  height: 22px;
  border: 1px solid rgba(0, 255, 255, 0.562);
  border-radius: 11px;
  overflow: hidden;
  background: linear-gradient(#123, rgba(0, 0, 0, 0.3));
  cursor: url(./Media/mouse-f2.png), auto;
  transition: 0.15s ease-in-out;
}
.current-progress {
  height: 100%;
  border-top-right-radius: 11px;
  border-bottom-right-radius: 11px;
  background: linear-gradient(
    to right,
    rgb(14, 207, 207),
    lightseagreen,
    rgb(36, 109, 138),
    rgb(98, 98, 211)
  );
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 13px;
  font-family: sans-serif;
  cursor: url(./Media/mouse-f2.png), auto;
  animation: animate 1.5s 1 cubic-bezier(0.075, 0.89, 0.165, 1);
  color: rgb(26, 10, 83);
}
@keyframes animate {
  from {
    width: 0;
  }
}
.progress:hover {
  transform: scale(1.05);
  height: auto;
  border: 1px solid rgba(0, 255, 255, 0.527);
  border-radius: 13px;
}
.progress:hover .current-progress {
  border: 1px solid rgba(0, 255, 255, 0.527);
  border-right: transparent;
}
.current-progress:hover {
  border: 1px solid rgba(0, 255, 255, 0.534);
}
.Skill-name {
  background: linear-gradient(
    to right,
    cyan,
    lightseagreen,
    rgb(64, 124, 204),
    rgb(32, 212, 212)
  );
  background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: 0.4s ease;
  font-weight: 750;
  font-style: oblique;
  letter-spacing: 2px;
  font-size: 14px;
  cursor: url(./Media/mouse-f2.png), auto;
}
.Skill-name:hover {
  transform: translateY(-4px);
}
@media only screen and (max-width: 550px) {
  .Container {
    width: 425px;
    height: 425px;
  }
  .progress {
    height: 18px;
    width: 90%;
  }
  .text {
    font-size: 2em;
  }
  .progress:hover {
    height: auto;
    border: 1px solid rgba(0, 255, 255, 0.527);
    border-radius: 13px;
  }
  .current-progress {
    font-size: 0.75rem;
  }
}
@media only screen and (max-width: 425px) {
  .Container {
    width: 350px;
    height: 350px;
  }
  .Skill-name {
    font-size: 13px;
  }
  .text {
    font-size: 1.8em;
  }
  .Container {
    flex-direction: column;
    min-height: 550px;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    padding: 10px 10px 20px;
  }
  .Box-1,
  .Box-2 {
    height: 50%;
    width: 100%;
  }
  .current-progress {
    height: 100%;
    font-size: 12px;
  }
  .progress {
    height: 20px;
  }
  .progress:hover,
  .progress:hover .current-progress {
    transform: scale(1.05);
    height: auto;
    opacity: 1;
  }
  .Container .current-progress:hover {
    height: auto;
  }
}
.MyName {
  cursor: url(./Media/mouse-f2.png), auto;
  background: linear-gradient(
    to right,
    cyan,
    rgb(39, 101, 182),
    lightseagreen,
    rgb(33, 236, 236)
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-style: oblique;
  position: relative;
  width: auto;
  height: 35px;
  font-weight: bold;
}
.MyName > span {
  transition: 0.2s cubic-bezier(0.075, 0.82, 0.865, 1);
}
.MyName:hover span:nth-child(1) {
  margin-right: 3px;
}

.MyName:hover span:nth-child(2) {
  margin-top: 3px;
}
.MyName:hover span:nth-child(3) {
  margin-left: 7px;
}
.MyName:hover span:nth-child(4) {
  margin-top: -4px;
}
.MyName:hover {
  transform: translateY(-5px);
}
.Heart-container {
  position: relative;
  height: 80%;
  width: 20%;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 80%;
  font-size: 30px;
  color: rgba(127, 255, 234, 0.603);
  flex-direction: column;
}
.Heart {
  cursor: url(./Media/mouse-f2.png), auto;
  transition: 0.2s ease;
  width: 100%;
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.Heart > svg:hover {
  color: rgb(216, 18, 51);
}
#CurrentCount {
  height: 50%;
  width: 100%;
  text-align: center;
  color: rgb(216, 18, 51);
  font-size: 27px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Twitter::before,
.Messenger::before {
  content: "Twitter";
  text-align: center;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 40px;
  border-radius: 7px;
  position: absolute;
  display: none;
  transform: translateX(-100%);
  background: rgba(0, 0, 0, 0.442);
  color: gray;
  cursor: url(./Media/mouse-f1.png), auto;
  opacity: 0;
  font-size: 18px;
}
.lds-ring {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 32px;
  height: 32px;
  margin: 4px;
  border: 4px solid rgb(216, 18, 51);
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: rgb(216, 18, 51) transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}
#avatar {
  border: 6px solid transparent;
  border-radius: 50%;
  background: url(./Media/Spaceship.png),
    linear-gradient(
      rgb(27, 187, 187),
      rgb(20, 104, 199),
      blue,
      rgb(20, 239, 255)
    );
  background-clip: padding-box, border-box;
  background-origin: padding-box, border-box;
  background-size: cover;
}
#avatar:hover {
  scale: 1.1;
}
@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.Messenger::before {
  content: "Messenger";
  font-size: 16px;
}
.Twitter:hover:before,
.Messenger:hover:before {
  display: flex;
  opacity: 1;
}
@media only screen and (max-width: 575px) {
  .Twitter::before,
  .Messenger::before {
    display: none;
  }
  .Twitter:hover:before,
  .Messenger:hover:before {
    display: none;
  }
}
section {
  border-bottom: 1px dashed lightskyblue;
}
@media (max-width: 425px) {
  .Heart-container {
    font-size: 26px;
  }
  .lds-ring {
    display: inline-block;
    position: relative;
    width: 30px;
    height: 30px;
  }
  .lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 24px;
    height: 24px;
    margin: 4px;
    border: 4px solid rgb(216, 18, 51);
    border-radius: 50%;
    animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: rgb(216, 18, 51) transparent transparent transparent;
  }
}
textarea {
  resize: none;
  width: 100%;
  height: 80%;
  padding: 5px 5px 10px 10px;
}
button,
a {
  cursor: url(./Media/mouse-f2.png), auto;
}
#DialogContent {
  width: 300px;
}
@media only screen and (max-width: 320px) {
  #DialogContent {
    width: 280px;
  }
}
.cube {
  position: relative;
  width: 20px;
  height: 20px;
  transform-style: preserve-3d;
  animation: animate 4s linear infinite;
}
.cube div {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
}
@keyframes animate {
  0% {
    transform: rotateX(-30deg) rotateY(0deg);
  }
  100% {
    transform: rotateX(-30deg) rotateY(360deg);
  }
}
.cube div span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(#123, cyan);
}
.top {
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  background: #134;
  transform: rotateX(90deg) translateZ(10px);
}
.top::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  background-color: cyan;
  transform: translateZ(-25px);
  filter: blur(10px);
}
